import React, { useRef } from "react";
import { Typography, Box, Grid, Avatar } from "@mui/material";
import { useSpring, animated } from "react-spring";
import { useInView } from "react-intersection-observer";
import Me from "../../assets/me.jpg";

const About = () => {
  const textRef = useRef(null);
  const imageRef = useRef(null);
  const [textInView] = useInView({
    triggerOnce: true,
  });
  const [imageInView] = useInView({
    triggerOnce: true,
  });

  const textSpring = useSpring({
    opacity: textInView ? 1 : 0,
    transform: textInView ? "translateX(0)" : "translateX(100%)",
    from: { opacity: 0, transform: "translateX(50%)" },
    config: { tension: 120, friction: 26 },
  });

  const imageSpring = useSpring({
    opacity: imageInView ? 1 : 0,
    transform: imageInView ? "translateY(0)" : "translateY(100%)",
    from: { opacity: 0, transform: "translateY(-50%)" },
    config: { tension: 120, friction: 26 },
  });

  return (
    <Box id="about" sx={{ width: "90%", margin: "auto" }}>
      <Grid container spacing={6}>
        <Grid item xs={12} sm={6}>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
              height: "100%",
            }}
          >
            <animated.div ref={textRef} style={textSpring}>
              <Typography
                sx={{
                  fontWeight: "700",
                  fontSize: { xs: "28px", sm: "36px" },
                }}
              >
                ABOUT
              </Typography>
              <Box
                sx={{
                  paddingTop: "5%",
                }}
              >
                <Typography sx={{ fontSize: { xs: "14px", sm: "18px" } }}>
                  I am 23 years old and I graduated at the Faculty of Electrical
                  Engineering,
                  <br />
                  University of Sarajevo with a Bachelor's degree in computer
                  science and informatics.
                </Typography>
                <Box
                  sx={{
                    paddingTop: "3%",
                  }}
                >
                  <Typography sx={{ fontSize: { xs: "14px", sm: "18px" } }}>
                    I worked as a freelancer for the American and German market.
                    I'm currently on the <br />
                    lookout for a full-time job or an internship opportunity to
                    enhance my skills and
                    <br />
                    learn programming within a professional setting. I'm eager
                    to grow and
                    <br />
                    contribute to a team while gaining valuable insights into
                    the world of
                    <br />
                    programming within a corporate environment.
                  </Typography>
                </Box>
              </Box>
            </animated.div>
          </Box>
        </Grid>
        <Grid item xs={12} sm={6}>
          <animated.div ref={imageRef} style={imageSpring}>
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "100%",
              }}
            >
              <Avatar
                src={Me}
                alt="me"
                variant="square"
                sx={{
                  width: { xs: "60vw", sm: "30vw" },
                  height: { xs: "60vw", sm: "30vw" },
                  borderRadius: "8px",
                }}
              />
            </Box>
          </animated.div>
        </Grid>
      </Grid>
    </Box>
  );
};

export default About;
