import React from "react";
import { Typography, Box } from "@mui/material";
import { useSpring, animated } from "react-spring";
import { useInView } from "react-intersection-observer";
import "./LandingPage.css";

const LandingPage = () => {
  const [ref, inView] = useInView({
    triggerOnce: true,
  });

  const props = useSpring({
    opacity: inView ? 1 : 0,
    transform: inView ? "translateX(0)" : "translateX(100%)",
    config: { tension: 120, friction: 50 },
  });
  return (
    <animated.div ref={ref} style={props}>
      <Box
        id="home"
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          width: "100%",
          height: "100vh",
          padding: "50px 0px",
          background:
            "radial-gradient(circle at 80% 50%, #fede59 1%, transparent 30%, transparent 70%, transparent 100%)",
        }}
      >
        <Box
          sx={{
            alignItems: "center",
          }}
        >
          <Typography
            sx={{
              fontSize: "3rem",
              fontWeight: 700,
            }}
          >
            HELLO!
          </Typography>
          <Typography
            sx={{
              fontSize: "2.5rem",
            }}
          >
            My name is Esma.
          </Typography>

          <Typography
            sx={{
              fontSize: "2.5rem",
            }}
          >
            I am software developer <br/> looking to create <br />
            better experience for users.
          </Typography>

        </Box>
      </Box>
    </animated.div>
  );
};

export default LandingPage;
